"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Textarea } from "@/components/ui/textarea";
import { Switch } from "@/components/ui/switch";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import { proveedoresMock, clientesMock, piezasMock, formatearPrecio } from "@/lib/data";
import { Cliente, Proveedor, Pieza } from "@/types";
import { Trash2, Plus } from "lucide-react";

interface PiezaFormulario {
  piezaId: string;
  cantidad: number;
  precioUnitario: number;
}

interface OrderFormProps {
  onSuccess?: () => void;
}

export default function OrderForm({ onSuccess }: OrderFormProps) {
  const [formData, setFormData] = useState({
    clienteId: "",
    clienteNuevo: {
      nombre: "",
      telefono: "",
      email: "",
      direccion: ""
    },
    esClienteNuevo: false,
    proveedorId: "",
    piezas: [] as PiezaFormulario[],
    pagado: false,
    montoSena: "",
    notas: "",
    fechaEntregaEstimada: ""
  });

  const [errors, setErrors] = useState<Record<string, string>>({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  const agregarPieza = () => {
    setFormData(prev => ({
      ...prev,
      piezas: [...prev.piezas, { piezaId: "", cantidad: 1, precioUnitario: 0 }]
    }));
  };

  const eliminarPieza = (index: number) => {
    setFormData(prev => ({
      ...prev,
      piezas: prev.piezas.filter((_, i) => i !== index)
    }));
  };

  const actualizarPieza = (index: number, campo: keyof PiezaFormulario, valor: any) => {
    setFormData(prev => ({
      ...prev,
      piezas: prev.piezas.map((pieza, i) => 
        i === index ? { ...pieza, [campo]: valor } : pieza
      )
    }));
  };

  const buscarClientePorTelefono = (telefono: string) => {
    const cliente = clientesMock.find(c => c.telefono === telefono);
    if (cliente) {
      setFormData(prev => ({
        ...prev,
        clienteId: cliente.id,
        esClienteNuevo: false
      }));
    } else {
      setFormData(prev => ({
        ...prev,
        clienteId: "",
        esClienteNuevo: true
      }));
    }
  };

  const calcularTotal = () => {
    return formData.piezas.reduce((total, pieza) => {
      return total + (pieza.cantidad * pieza.precioUnitario);
    }, 0);
  };

  const validarFormulario = () => {
    const newErrors: Record<string, string> = {};

    if (formData.esClienteNuevo) {
      if (!formData.clienteNuevo.nombre) newErrors.nombre = "El nombre es obligatorio";
      if (!formData.clienteNuevo.telefono) newErrors.telefono = "El teléfono es obligatorio";
    } else {
      if (!formData.clienteId) newErrors.cliente = "Debe seleccionar un cliente";
    }

    if (!formData.proveedorId) newErrors.proveedor = "Debe seleccionar un proveedor";
    if (formData.piezas.length === 0) newErrors.piezas = "Debe agregar al menos una pieza";
    
    formData.piezas.forEach((pieza, index) => {
      if (!pieza.piezaId) newErrors[`pieza_${index}`] = "Debe seleccionar una pieza";
      if (pieza.cantidad <= 0) newErrors[`cantidad_${index}`] = "La cantidad debe ser mayor a 0";
      if (pieza.precioUnitario <= 0) newErrors[`precio_${index}`] = "El precio debe ser mayor a 0";
    });

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validarFormulario()) return;

    setIsSubmitting(true);
    
    try {
      // Simular envío del formulario
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      console.log("Pedido creado:", {
        ...formData,
        montoTotal: calcularTotal(),
        fechaPedido: new Date()
      });

      // Resetear formulario
      setFormData({
        clienteId: "",
        clienteNuevo: { nombre: "", telefono: "", email: "", direccion: "" },
        esClienteNuevo: false,
        proveedorId: "",
        piezas: [],
        pagado: false,
        montoSena: "",
        notas: "",
        fechaEntregaEstimada: ""
      });

      onSuccess?.();
    } catch (error) {
      console.error("Error al crear pedido:", error);
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-6">
      {/* Información del Cliente */}
      <Card>
        <CardHeader>
          <CardTitle>Información del Cliente</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="flex items-center space-x-2">
            <Switch
              checked={formData.esClienteNuevo}
              onCheckedChange={(checked) => 
                setFormData(prev => ({ ...prev, esClienteNuevo: checked }))
              }
            />
            <Label>Cliente nuevo</Label>
          </div>

          {formData.esClienteNuevo ? (
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <Label htmlFor="nombre">Nombre *</Label>
                <Input
                  id="nombre"
                  value={formData.clienteNuevo.nombre}
                  onChange={(e) => setFormData(prev => ({
                    ...prev,
                    clienteNuevo: { ...prev.clienteNuevo, nombre: e.target.value }
                  }))}
                  placeholder="Nombre completo del cliente"
                />
                {errors.nombre && <p className="text-red-500 text-sm mt-1">{errors.nombre}</p>}
              </div>
              
              <div>
                <Label htmlFor="telefono">Teléfono *</Label>
                <Input
                  id="telefono"
                  value={formData.clienteNuevo.telefono}
                  onChange={(e) => {
                    const telefono = e.target.value;
                    setFormData(prev => ({
                      ...prev,
                      clienteNuevo: { ...prev.clienteNuevo, telefono }
                    }));
                    buscarClientePorTelefono(telefono);
                  }}
                  placeholder="Número de teléfono"
                />
                {errors.telefono && <p className="text-red-500 text-sm mt-1">{errors.telefono}</p>}
              </div>

              <div>
                <Label htmlFor="email">Email</Label>
                <Input
                  id="email"
                  type="email"
                  value={formData.clienteNuevo.email}
                  onChange={(e) => setFormData(prev => ({
                    ...prev,
                    clienteNuevo: { ...prev.clienteNuevo, email: e.target.value }
                  }))}
                  placeholder="Correo electrónico"
                />
              </div>

              <div>
                <Label htmlFor="direccion">Dirección</Label>
                <Input
                  id="direccion"
                  value={formData.clienteNuevo.direccion}
                  onChange={(e) => setFormData(prev => ({
                    ...prev,
                    clienteNuevo: { ...prev.clienteNuevo, direccion: e.target.value }
                  }))}
                  placeholder="Dirección completa"
                />
              </div>
            </div>
          ) : (
            <div>
              <Label htmlFor="cliente">Cliente Existente *</Label>
              <Select
                value={formData.clienteId}
                onValueChange={(value) => setFormData(prev => ({ ...prev, clienteId: value }))}
              >
                <SelectTrigger>
                  <SelectValue placeholder="Seleccionar cliente" />
                </SelectTrigger>
                <SelectContent>
                  {clientesMock.map((cliente) => (
                    <SelectItem key={cliente.id} value={cliente.id}>
                      {cliente.nombre} - {cliente.telefono}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
              {errors.cliente && <p className="text-red-500 text-sm mt-1">{errors.cliente}</p>}
            </div>
          )}
        </CardContent>
      </Card>

      {/* Información del Proveedor */}
      <Card>
        <CardHeader>
          <CardTitle>Proveedor</CardTitle>
        </CardHeader>
        <CardContent>
          <div>
            <Label htmlFor="proveedor">Proveedor *</Label>
            <Select
              value={formData.proveedorId}
              onValueChange={(value) => setFormData(prev => ({ ...prev, proveedorId: value }))}
            >
              <SelectTrigger>
                <SelectValue placeholder="Seleccionar proveedor" />
              </SelectTrigger>
              <SelectContent>
                {proveedoresMock.map((proveedor) => (
                  <SelectItem key={proveedor.id} value={proveedor.id}>
                    {proveedor.nombre} - {proveedor.contacto}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
            {errors.proveedor && <p className="text-red-500 text-sm mt-1">{errors.proveedor}</p>}
          </div>
        </CardContent>
      </Card>

      {/* Piezas del Pedido */}
      <Card>
        <CardHeader>
          <div className="flex justify-between items-center">
            <CardTitle>Piezas del Pedido</CardTitle>
            <Button type="button" onClick={agregarPieza} variant="outline" size="sm">
              <Plus className="w-4 h-4 mr-2" />
              Agregar Pieza
            </Button>
          </div>
        </CardHeader>
        <CardContent className="space-y-4">
          {formData.piezas.length === 0 ? (
            <p className="text-gray-500 text-center py-4">
              No hay piezas agregadas. Haz clic en "Agregar Pieza" para comenzar.
            </p>
          ) : (
            formData.piezas.map((pieza, index) => (
              <div key={index} className="border rounded-lg p-4 space-y-4">
                <div className="flex justify-between items-center">
                  <h4 className="font-medium">Pieza #{index + 1}</h4>
                  <Button
                    type="button"
                    onClick={() => eliminarPieza(index)}
                    variant="outline"
                    size="sm"
                    className="text-red-600 hover:text-red-700"
                  >
                    <Trash2 className="w-4 h-4" />
                  </Button>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                  <div>
                    <Label>Pieza *</Label>
                    <Select
                      value={pieza.piezaId}
                      onValueChange={(value) => {
                        const piezaSeleccionada = piezasMock.find(p => p.id === value);
                        actualizarPieza(index, 'piezaId', value);
                        if (piezaSeleccionada) {
                          actualizarPieza(index, 'precioUnitario', piezaSeleccionada.precio);
                        }
                      }}
                    >
                      <SelectTrigger>
                        <SelectValue placeholder="Seleccionar pieza" />
                      </SelectTrigger>
                      <SelectContent>
                        {piezasMock.map((piezaOption) => (
                          <SelectItem key={piezaOption.id} value={piezaOption.id}>
                            {piezaOption.referencia} - {piezaOption.descripcion}
                          </SelectItem>
                        ))}
                      </SelectContent>
                    </Select>
                    {errors[`pieza_${index}`] && (
                      <p className="text-red-500 text-sm mt-1">{errors[`pieza_${index}`]}</p>
                    )}
                  </div>

                  <div>
                    <Label>Cantidad *</Label>
                    <Input
                      type="number"
                      min="1"
                      value={pieza.cantidad}
                      onChange={(e) => actualizarPieza(index, 'cantidad', parseInt(e.target.value) || 0)}
                    />
                    {errors[`cantidad_${index}`] && (
                      <p className="text-red-500 text-sm mt-1">{errors[`cantidad_${index}`]}</p>
                    )}
                  </div>

                  <div>
                    <Label>Precio Unitario *</Label>
                    <Input
                      type="number"
                      step="0.01"
                      min="0"
                      value={pieza.precioUnitario}
                      onChange={(e) => actualizarPieza(index, 'precioUnitario', parseFloat(e.target.value) || 0)}
                    />
                    {errors[`precio_${index}`] && (
                      <p className="text-red-500 text-sm mt-1">{errors[`precio_${index}`]}</p>
                    )}
                  </div>
                </div>

                <div className="text-right">
                  <Badge variant="secondary">
                    Subtotal: {formatearPrecio(pieza.cantidad * pieza.precioUnitario)}
                  </Badge>
                </div>
              </div>
            ))
          )}
          
          {errors.piezas && <p className="text-red-500 text-sm">{errors.piezas}</p>}
          
          {formData.piezas.length > 0 && (
            <>
              <Separator />
              <div className="text-right">
                <div className="text-lg font-semibold">
                  Total: {formatearPrecio(calcularTotal())}
                </div>
              </div>
            </>
          )}
        </CardContent>
      </Card>

      {/* Información de Pago */}
      <Card>
        <CardHeader>
          <CardTitle>Información de Pago</CardTitle>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="flex items-center space-x-2">
            <Switch
              checked={formData.pagado}
              onCheckedChange={(checked) => setFormData(prev => ({ ...prev, pagado: checked }))}
            />
            <Label>Pedido pagado completamente</Label>
          </div>

          {!formData.pagado && (
            <div>
              <Label htmlFor="sena">Monto de Seña</Label>
              <Input
                id="sena"
                type="number"
                step="0.01"
                min="0"
                value={formData.montoSena}
                onChange={(e) => setFormData(prev => ({ ...prev, montoSena: e.target.value }))}
                placeholder="Cantidad dejada como seña"
              />
            </div>
          )}

          <div>
            <Label htmlFor="fechaEntrega">Fecha de Entrega Estimada</Label>
            <Input
              id="fechaEntrega"
              type="date"
              value={formData.fechaEntregaEstimada}
              onChange={(e) => setFormData(prev => ({ ...prev, fechaEntregaEstimada: e.target.value }))}
            />
          </div>

          <div>
            <Label htmlFor="notas">Notas Adicionales</Label>
            <Textarea
              id="notas"
              value={formData.notas}
              onChange={(e) => setFormData(prev => ({ ...prev, notas: e.target.value }))}
              placeholder="Información adicional sobre el pedido..."
              rows={3}
            />
          </div>
        </CardContent>
      </Card>

      {/* Botones de Acción */}
      <div className="flex justify-end space-x-4">
        <Button type="button" variant="outline" onClick={() => onSuccess?.()}>
          Cancelar
        </Button>
        <Button type="submit" disabled={isSubmitting}>
          {isSubmitting ? "Guardando..." : "Crear Pedido"}
        </Button>
      </div>
    </form>
  );
}
