// Funciones utilitarias
export const obtenerClientePorTelefono = (telefono: string): Cliente | undefined => {
  return clientesMock.find(cliente => cliente.telefono === telefono);
};

export const obtenerProveedorPorId = (id: string): Proveedor | undefined => {
  return proveedoresMock.find(proveedor => proveedor.id === id);
};

export const obtenerPiezaPorReferencia = (referencia: string): Pieza | undefined => {
  return piezasMock.find(pieza => pieza.referencia === referencia);
};

export const calcularTotalPedido = (piezas: PiezaPedido[]): number => {
  return piezas.reduce((total, pieza) => total + pieza.subtotal, 0);
};

export const formatearPrecio = (precio: number): string => {
  return new Intl.NumberFormat('es-ES', {
    style: 'currency',
    currency: 'EUR'
  }).format(precio);
};

export const formatearFecha = (fecha: Date): string => {
  return new Intl.DateTimeFormat('es-ES', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit'
  }).format(fecha);
};
