"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { pedidosMock, formatearPrecio, formatearFecha } from "@/lib/data";
import { Pedido } from "@/types";
import { Search, Edit, Eye, Filter } from "lucide-react";

export default function OrderTable() {
  const [pedidos] = useState<Pedido[]>(pedidosMock);
  const [filtros, setFiltros] = useState({
    busqueda: "",
    estado: "todos",
    proveedor: "todos",
    pagado: "todos"
  });
  const [pedidoSeleccionado, setPedidoSeleccionado] = useState<Pedido | null>(null);

  const pedidosFiltrados = pedidos.filter(pedido => {
    const coincideBusqueda = !filtros.busqueda || 
      pedido.cliente.nombre.toLowerCase().includes(filtros.busqueda.toLowerCase()) ||
      pedido.cliente.telefono.includes(filtros.busqueda) ||
      pedido.id.toLowerCase().includes(filtros.busqueda.toLowerCase());

    const coincideEstado = !filtros.estado || filtros.estado === "todos" || pedido.estado === filtros.estado;
    const coincideProveedor = !filtros.proveedor || filtros.proveedor === "todos" || pedido.proveedor.id === filtros.proveedor;
    const coincidePagado = !filtros.pagado || filtros.pagado === "todos" || 
      (filtros.pagado === "pagado" && pedido.pagado) ||
      (filtros.pagado === "pendiente" && !pedido.pagado);

    return coincideBusqueda && coincideEstado && coincideProveedor && coincidePagado;
  });

  const obtenerBadgeEstado = (estado: string) => {
    const estilos = {
      pendiente: "bg-yellow-100 text-yellow-800",
      en_proceso: "bg-blue-100 text-blue-800",
      completado: "bg-green-100 text-green-800",
      cancelado: "bg-red-100 text-red-800"
    };

    const textos = {
      pendiente: "Pendiente",
      en_proceso: "En Proceso",
      completado: "Completado",
      cancelado: "Cancelado"
    };

    return (
      <Badge className={estilos[estado as keyof typeof estilos]}>
        {textos[estado as keyof typeof textos]}
      </Badge>
    );
  };

  const obtenerBadgePago = (pagado: boolean) => {
    return pagado ? (
      <Badge className="bg-green-100 text-green-800">Pagado</Badge>
    ) : (
      <Badge className="bg-red-100 text-red-800">Pendiente</Badge>
    );
  };

  const limpiarFiltros = () => {
    setFiltros({
      busqueda: "",
      estado: "todos",
      proveedor: "todos",
      pagado: "todos"
    });
  };

  return (
    <div className="space-y-4">
      {/* Filtros */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Filter className="w-4 h-4" />
            Filtros de Búsqueda
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
            <div>
              <Label htmlFor="busqueda">Buscar</Label>
              <div className="relative">
                <Search className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
                <Input
                  id="busqueda"
                  placeholder="Nombre, teléfono o ID..."
                  value={filtros.busqueda}
                  onChange={(e) => setFiltros(prev => ({ ...prev, busqueda: e.target.value }))}
                  className="pl-10"
                />
              </div>
            </div>

            <div>
              <Label htmlFor="estado">Estado</Label>
              <Select
                value={filtros.estado}
                onValueChange={(value) => setFiltros(prev => ({ ...prev, estado: value }))}
              >
                <SelectTrigger>
                  <SelectValue placeholder="Todos los estados" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="todos">Todos los estados</SelectItem>
                  <SelectItem value="pendiente">Pendiente</SelectItem>
                  <SelectItem value="en_proceso">En Proceso</SelectItem>
                  <SelectItem value="completado">Completado</SelectItem>
                  <SelectItem value="cancelado">Cancelado</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div>
              <Label htmlFor="proveedor">Proveedor</Label>
              <Select
                value={filtros.proveedor}
                onValueChange={(value) => setFiltros(prev => ({ ...prev, proveedor: value }))}
              >
                <SelectTrigger>
                  <SelectValue placeholder="Todos los proveedores" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="todos">Todos los proveedores</SelectItem>
                  <SelectItem value="1">AutoPartes Madrid</SelectItem>
                  <SelectItem value="2">Repuestos Barcelona</SelectItem>
                  <SelectItem value="3">Piezas Valencia</SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div>
              <Label htmlFor="pagado">Estado de Pago</Label>
              <Select
                value={filtros.pagado}
                onValueChange={(value) => setFiltros(prev => ({ ...prev, pagado: value }))}
              >
                <SelectTrigger>
                  <SelectValue placeholder="Todos los pagos" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="todos">Todos los pagos</SelectItem>
                  <SelectItem value="pagado">Pagado</SelectItem>
                  <SelectItem value="pendiente">Pendiente</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>

          <div className="flex justify-between items-center mt-4">
            <p className="text-sm text-gray-600">
              Mostrando {pedidosFiltrados.length} de {pedidos.length} pedidos
            </p>
            <Button variant="outline" onClick={limpiarFiltros}>
              Limpiar Filtros
            </Button>
          </div>
        </CardContent>
      </Card>

      {/* Tabla de Pedidos */}
      <div className="border rounded-lg">
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>ID</TableHead>
              <TableHead>Cliente</TableHead>
              <TableHead>Teléfono</TableHead>
              <TableHead>Proveedor</TableHead>
              <TableHead>Fecha</TableHead>
              <TableHead>Estado</TableHead>
              <TableHead>Pago</TableHead>
              <TableHead>Total</TableHead>
              <TableHead>Acciones</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {pedidosFiltrados.length === 0 ? (
              <TableRow>
                <TableCell colSpan={9} className="text-center py-8 text-gray-500">
                  No se encontraron pedidos que coincidan con los filtros
                </TableCell>
              </TableRow>
            ) : (
              pedidosFiltrados.map((pedido) => (
                <TableRow key={pedido.id}>
                  <TableCell className="font-mono text-sm">
                    #{pedido.id}
                  </TableCell>
                  <TableCell className="font-medium">
                    {pedido.cliente.nombre}
                  </TableCell>
                  <TableCell>{pedido.cliente.telefono}</TableCell>
                  <TableCell>{pedido.proveedor.nombre}</TableCell>
                  <TableCell>{formatearFecha(pedido.fechaPedido)}</TableCell>
                  <TableCell>{obtenerBadgeEstado(pedido.estado)}</TableCell>
                  <TableCell>{obtenerBadgePago(pedido.pagado)}</TableCell>
                  <TableCell className="font-semibold">
                    {formatearPrecio(pedido.montoTotal)}
                  </TableCell>
                  <TableCell>
                    <div className="flex space-x-2">
                      <Dialog>
                        <DialogTrigger asChild>
                          <Button
                            variant="outline"
                            size="sm"
                            onClick={() => setPedidoSeleccionado(pedido)}
                          >
                            <Eye className="w-4 h-4" />
                          </Button>
                        </DialogTrigger>
                        <DialogContent className="max-w-2xl">
                          <DialogHeader>
                            <DialogTitle>Detalles del Pedido #{pedido.id}</DialogTitle>
                          </DialogHeader>
                          {pedidoSeleccionado && (
                            <div className="space-y-4">
                              <div className="grid grid-cols-2 gap-4">
                                <div>
                                  <h4 className="font-semibold">Cliente</h4>
                                  <p>{pedidoSeleccionado.cliente.nombre}</p>
                                  <p className="text-sm text-gray-600">{pedidoSeleccionado.cliente.telefono}</p>
                                  {pedidoSeleccionado.cliente.email && (
                                    <p className="text-sm text-gray-600">{pedidoSeleccionado.cliente.email}</p>
                                  )}
                                </div>
                                <div>
                                  <h4 className="font-semibold">Proveedor</h4>
                                  <p>{pedidoSeleccionado.proveedor.nombre}</p>
                                  <p className="text-sm text-gray-600">{pedidoSeleccionado.proveedor.contacto}</p>
                                </div>
                              </div>

                              <div>
                                <h4 className="font-semibold mb-2">Piezas</h4>
                                <div className="space-y-2">
                                  {pedidoSeleccionado.piezas.map((pieza) => (
                                    <div key={pieza.id} className="flex justify-between items-center p-2 bg-gray-50 rounded">
                                      <div>
                                        <p className="font-medium">{pieza.pieza.referencia}</p>
                                        <p className="text-sm text-gray-600">{pieza.pieza.descripcion}</p>
                                      </div>
                                      <div className="text-right">
                                        <p>{pieza.cantidad} x {formatearPrecio(pieza.precioUnitario)}</p>
                                        <p className="font-semibold">{formatearPrecio(pieza.subtotal)}</p>
                                      </div>
                                    </div>
                                  ))}
                                </div>
                              </div>

                              <div className="grid grid-cols-2 gap-4">
                                <div>
                                  <h4 className="font-semibold">Estado</h4>
                                  {obtenerBadgeEstado(pedidoSeleccionado.estado)}
                                </div>
                                <div>
                                  <h4 className="font-semibold">Pago</h4>
                                  {obtenerBadgePago(pedidoSeleccionado.pagado)}
                                  {!pedidoSeleccionado.pagado && pedidoSeleccionado.montoSena && (
                                    <p className="text-sm text-gray-600">
                                      Seña: {formatearPrecio(pedidoSeleccionado.montoSena)}
                                    </p>
                                  )}
                                </div>
                              </div>

                              <div className="border-t pt-4">
                                <div className="flex justify-between items-center">
                                  <span className="text-lg font-semibold">Total:</span>
                                  <span className="text-lg font-bold">{formatearPrecio(pedidoSeleccionado.montoTotal)}</span>
                                </div>
                              </div>

                              {pedidoSeleccionado.notas && (
                                <div>
                                  <h4 className="font-semibold">Notas</h4>
                                  <p className="text-gray-600">{pedidoSeleccionado.notas}</p>
                                </div>
                              )}
                            </div>
                          )}
                        </DialogContent>
                      </Dialog>

                      <Button variant="outline" size="sm">
                        <Edit className="w-4 h-4" />
                      </Button>
                    </div>
                  </TableCell>
                </TableRow>
              ))
            )}
          </TableBody>
        </Table>
      </div>
    </div>
  );
}
